<template>
 <div id="new-employee">
  <h3>New Employee</h3>
  <div class="row">
   <form @submit.prevent="saveEmployee" class="col s12">
    <div class="row">
     <div class="input-field col s12">
      <input type="text" v-model="employee.id" required>
      <label>Employee ID</label>
     </div>
    </div>
    <div class="row">
     <div class="input-field col s12">
      <input type="text" v-model="employee.name" required>
      <label>name</label>
     </div>
    </div>
    <div class="row">
     <div class="input-field col s12">
      <input type="text" v-model="employee.email" required>
      <label>Email</label>
     </div>
    </div>
    <div class="row">
     <div class="input-field col s12">
      <input type="text" v-model="employee.department" required>
      <label>Department</label>
     </div>
    </div>
    <div class="row">
     <div class="input-field col s12">
      <input type="text" v-model="employee.position" required>
      <label>Position</label>
     </div>
    </div>
    <button class="btn" type="submit">Submit</button>
    <router-link class="btn grey" to="/">Cancel</router-link>
   </form>
  </div> 
 </div>
</template>
<script>
import db from '../firebase_services/firebaseinit'
export default {
 name:'new-employee',
 data(){
  return{
   employee:{
    id:'',
    email:'',
    position:'',
    name:'',
    department:''
   }
  }
 },
 methods:{
  saveEmployee(){
   db.collection('employees').add(this.employee)
   .then(docRef=> this.$router.push('/'))
   .catch(error=> console.log(error))
  }
 }
}
</script>